.home-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.table-naics {

  th,
  td {
    background-color: transparent;
  }

  th:first-child,
  td:first-child {
    width: 150px;
  }
}